@charset "UTF-8";
.mark {
  position: relative;
}
.mark .mark-text {
  white-space: nowrap;
}
.mark[class*=al-]::before {
  content: "";
  transition: 0.3s ease;
}
.mark[class*=ar-]::after {
  content: "";
  transition: 0.3s ease;
}
.mark.module-a {
  margin-bottom: 2rem;
}
.mark.module-a .mark-text {
  padding-left: 1.45rem;
}
.mark.module-a.style-a {
  position: relative;
}
.mark.module-a.style-a::before {
  content: "";
  display: block;
  width: 1.4rem;
  height: 1.4rem;
  position: absolute;
  top: 50%;
  left: -0.5rem;
  transform: translateY(-50%);
  border-radius: 0.2rem;
}
.mark.module-a.style-b {
  position: relative;
}
.mark.module-a.style-b::before {
  content: "";
  display: block;
  width: 1.4rem;
  height: 1.4rem;
  background: rgba(34, 188, 98, 0.4);
  position: absolute;
  top: 50%;
  left: -0.5rem;
  transform: translateY(-50%);
  border-radius: 0.2rem;
}
.mark.module-a.type-a .mark-text {
  font-weight: 700;
  font-size: 1.4rem;
  color: rgb(57, 57, 57);
  line-height: 120%;
}
@media (max-width: 1023px) {
  .mark.module-a.type-a .mark-text {
    font-weight: 700;
    font-size: 1.2rem;
  }
}
.mark.module-a[class*=color1]::before {
  background: rgba(5, 104, 204, 0.35);
}
.mark-display .mark-list {
  display: flex;
  flex-wrap: wrap;
}
.mark-display.style-a .mark-list {
  gap: 1.6rem;
}
@media (max-width: 1023px) {
  .mark-display.style-a .mark-list {
    gap: 0.8rem 1.2rem;
  }
}
