.btn {
  margin: 0;
  padding: 0;
  cursor: pointer;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
.btn.module-a {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  transition: all 0.2s ease-out;
}
.btn.module-a.style-a {
  padding: 1.4rem 4.7rem;
  font-weight: 700;
  font-size: 1.6rem;
  border-radius: 3.1rem;
}
.btn.module-a.color-a {
  border: 0.1rem solid rgb(0, 0, 0);
  color: rgb(0, 0, 0);
}
.btn.module-a.color-a:hover, .btn.module-a.color-a:focus {
  border: 0.1rem solid rgb(0, 0, 0);
  background-color: rgb(0, 0, 0);
  color: rgb(255, 255, 255);
}
.btn.module-a.color-b {
  border: 0.1rem solid rgb(255, 255, 255);
  color: rgb(255, 255, 255);
}
.btn.module-a.color-b:hover, .btn.module-a.color-b:focus {
  border: 0.1rem solid transparent;
  background-color: rgba(255, 255, 255, 0.5);
}
.btn.icon {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-size: 0;
  line-height: 0;
}
.btn.icon::after {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: 50%;
}
.btn.icon.style-a {
  width: 1.6rem;
  height: 1.6rem;
}
.btn.icon.style-a.play::after {
  background: url("data:image/svg+xml,%0A%3Csvg width='12' height='12' viewBox='0 0 12 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M3.33464 0.666748H0.667969V11.3334H3.33464V0.666748ZM11.3346 0.666748H8.66797V11.3334H11.3346V0.666748Z' fill='rgb(255,255,255)'/%3E%3C/svg%3E%0A") no-repeat center;
  background-size: 1.2rem;
}
.btn.icon.style-a.stop::after {
  background: url("data:image/svg+xml,%0A%3Csvg width='16' height='17' viewBox='0 0 16 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M14 8.5L3.5 14.5622L3.5 2.43782L14 8.5Z' fill='rgb(255,255,255)'/%3E%3C/svg%3E%0A") no-repeat center;
  background-size: 1.6rem;
}
.btn.icon.style-b {
  width: 4.8rem;
  height: 4.8rem;
  border-radius: 50%;
  transition: all 0.2s ease-out;
}
.btn.icon.style-b::after {
  background-repeat: no-repeat;
  background-position: center center;
}
.btn.icon.style-b.arrow-left1.color-a {
  border: 0.1rem solid rgba(255, 255, 255, 0.5);
