  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (max-width: 1023px) {
  #header .header-inner .site-navi {
    order: 3;
    align-items: flex-start;
    position: absolute;
    top: 6.4rem;
    height: calc(100vh - 6.4rem);
    margin: 0 -1.8rem;
    overflow: auto;
    display: none;
  }
}
#header .header-inner .site-navi .depth1-list {
